.ButtonBar{
    display: flex;
    flex-direction: row;
    align-items: right;
    justify-content: right;
    font-size: calc(10px + 2vmin);
    gap:1%;
  }
  
  .ButtonDiv{
    height: 1%;
  }

  .ButtinLink{
    display: inline-block;
    width: fit-content;
    height: fit-content;
  }
  
  .icon{
    height:50px;
    width: 50px;
    display: block;
  }
  
  .Name{
    font-size: calc(10px + 5vmin);  
    font-weight: 700;
    margin: 2%;
    text-align: left;
    justify-content: left;
    justify-content: left;
  }
  
  .Intro{
    font-size: calc(2px + 2vmin);
    color: #8fa8c6;
    letter-spacing: 1px;
    font-weight: 500;
    text-align: left;
    margin: 2%;
  }

  .NavBar{
    text-align: left;
    padding-left: 10%;
    font-size: calc(2px + 2vmin);
    color:#FFFFFF;
    margin: 2%;
  }

  .Nav:hover{
    color: #C17C74;
  }