.Home{
    display: flex;
    flex-direction: column;
    line-height: 9vmin;
  }
  
  .Welcome{
    font-weight:bold;
    font-size: calc(10vmin);
  }
  
  .WelcomeShadow{
    font-weight:bold;
    font-size: calc(10vmin);
    transform: scaleY(-1);
    filter: blur(15px);
    color: #3d4453;
  }
  
  .Scroll{
    display: flex;
    font-size: calc(2px + 2vmin);
    text-align: left;
  }
  
  .ScrollItem{
    display: flex;
    flex-direction: column;
    font-size: calc(2px + 2vmin);
    text-align: left;
  }

  .Template{
    padding-top:2vh;
    padding-bottom:2vh;
  }
  
  .Title{
    font-weight:700;
  }
  
  .CompDate{
    color: #C17C74;
    font-size: calc(2px + 1.5vmin);
  }
  
  .Points{
    padding:2vh;
    font-size: calc(4px + 1.5vmin);
    align-items: center;
    justify-content: center;
    line-height: 2.8vh;
  }

  #Project{
    padding-bottom: 0;
  }

  .SkillPills{
    padding:.25rem;
  }

  .ProjectSkills{
    display: flex;
    flex-direction: row;
    margin: 1%;
    font-size: calc(4px + 1.5vmin);
    flex-wrap: wrap;
  }

  .Pills{
    justify-content: center;
    align-items: center;
    margin:1%;
    line-height: 1.25rem;
    padding-block: .25rem;
    padding-inline:.75rem;
    border-radius: 9999px;
    background-color: rgba(193, 124, 116, 0.8);
    color:#131a2c;
    font-weight: 600;
    white-space: nowrap;
  }
  
  .SkillPoints{
    padding-top:0;
    font-size: calc(4px + 1.5vmin);
    padding-left:2vw;
    padding-right:2vw;
    align-items: center;
    justify-content: center;
    line-height: 2.8vh;
  }

  .SkillRow{
    display: flex;
    flex-direction: row;
  }
